.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  height: 50px;
  background-color: #4682b4;
  display: flex;
  align-items: center;
}

.container {
  max-width: 97%;
  width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.link {
  text-decoration: none;
  padding: 12px;
  color: #fff;
  font-size: 22px;
}

.active {
  color: #ffffff33;
}
